@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');

* {
  font-family: 'Montserrat', sans-serif;
}

nav a {
    color: white!important;
}

.App-title {
    font-size: 1.5em;
    color: #fff;
    font-style: normal;
    font-weight: 700;
    font-size: 96px;
    line-height: 99px;
}

.Navbar-title {
    color: #fff;
    font-size: 15px;
}

body {
    background-image: url("https://raw.githubusercontent.com/vsecoder/vsecoder/main/background.webp");
    background-position: center;
    background-repeat: repeat;
    background-size: auto;
    height: 100%;
}


.App-logo {
    margin-top: -500px;
}

button {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 21px;
    margin: 20px;
    padding: 15px!important;
}

.button1 {
    border: 2.5px solid #fff;
    border-radius: 16px!important;

    cursor: pointer;
}

h1 {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 56px;
    line-height: 99px;
}

.left-side {
    margin-top: 100px;
    max-width: 400px;
    display: inline-block;
}

.left-side p {
    border-left: 2px solid #fff;
    padding-left: 30px;
    margin-top: 40px;
    margin-left: 10px;
    margin-bottom: 40px;
    font-family: 'Montserrat';
    font-style: normal;
    font-size: 18px;
    line-height: 21px;
    color: rgb(235, 235, 235);
}

.right-side {
    max-width: 300px;
    display: inline-block;
}

.right-side-phone {
    display: none;
}

.Header {
    margin-top: 200px;
}

.About {
    padding-top: 130px;
    padding-bottom: 50px;
    color: white;
}

.About-title {
    font-size: 64px;
    text-align: center;
    margin-bottom: 40px;
}

.About-text {
    font-size: 20px;
    text-align: center;
    line-height: 28px;
}

.white {
    background-color: white;
    color: #000000;
}

.white p, .white h3, .white h4 {
    color: #000000;
}


@media screen and (max-width: 768px) {

    .App-logo {
        margin-top: 0;
        width: 100%;
        text-align: center;
        margin: 0 auto;
    }

    .right-side {
        display: none;
    }

    .left-side {
        width: 100%;
    }

    .right-side-phone {
        display: block;
        margin: 0 auto;
        width: 100%;
        text-align: center;
    }

    .About {
        padding-top: 80px;
        padding-bottom: 10px;
        color: white;
    }

    .Header {
        margin-top: 10px;
    }
    
    .left-side {
        margin-top: 5px;
    }

    .App-header {
        margin-top: -60px;
        margin-bottom: 15px;
    }

    .App-title {
        font-size: 60px;
        text-align: center;
    }
    .line-right {
        padding-left: 20px;
    }
    .line {
        width: 90%;
 
    }
}
    
.Banner {
    background-color: #ecdb85;
    color: black!important;
    padding-top: 50px;
    padding-bottom: 50px;
}

.Banner h2, h4 {
    display: inline-block;
}

.Banner h2 {
    float: right;
}